.banner{
    height: 448px;
    position: relative;
    color: white;
    object-fit: contain;
} 
.banner__contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}
.banner__title{
    font-weight: 800;
    font-size: 3rem;
    padding-bottom: 0.3rem;
}
.banner__description{
width: 45rem;
line-height: 1.3;
padding-top: 1rem;
font-size: 0.8rem;
max-width: 22rem;
height: 5rem;
}
.banner__fadeBottom{
 height: 7.4rem;
  background-image: linear-gradient(
    180deg,
    transparent,
    rgb(37, 37, 37, 0.61),
    #111
  );
}
.banner__button{
   cursor: pointer;
   color: #fff;
   outline: none;
   border: none;
   font-weight: 700;
   border-radius: 0.2vw;
  padding: 0.5rem 2rem ;
   margin-right: 1rem;
   background-color:rgba(51, 51, 51, 0.5);
}
.banner__button:hover{
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;

}
